.dropdown-submenu {
  position: relative; }

.dropdown-submenu > .dropdown-menu {
  margin-top: -6px;
  margin-left: -1px; }
  .dropdown-submenu > .dropdown-menu.dropdown-menu-level-0 {
    left: 0; }
  .dropdown-submenu >
.dropdown-menu.dropdown-menu-level-1, .dropdown-submenu > .dropdown-menu.dropdown-menu-level-2 {
    left: 100%;
    top: 0; }
  .dropdown-submenu > .dropdown-menu .dropdown-submenu > .dropdown-item:after {
    display: block;
    content: " ";
    float: right;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 5px 0 5px 5px;
    border-left-color: #ccc;
    margin-top: 5px;
    margin-right: -10px; }

.dropdown-submenu:hover > .dropdown-menu {
  display: block; }

.dropdown-submenu:hover > .dropdown-item:after {
  border-left-color: #fff; }

.dropdown-submenu.pull-left {
  float: none; }

.dropdown-submenu.pull-left > .dropdown-menu {
  left: -100%;
  margin-left: 10px; }
